<template>
  <div class="page-content" v-loading="loading">
    <el-row :gutter="60">
      <el-col :lg="12">
        <div class="sub-header">
          <div class="sub-header-title"></div>
          <div class="sub-header-fd">
            <el-button
              size="medium"
              :type="!isEdit ? '' : 'primary'"
              @click="handleEdit"
            >
              {{ !isEdit ? "" : "" }}
            </el-button>
          </div>
        </div>
        <el-form label-position="top" size="mini" :model="course">
          <el-row :gutter="20">
            <el-col :lg="12">
              <el-form-item label="">
                <el-input :disabled="!isEdit" v-model="course.name"></el-input>
              </el-form-item>
            </el-col>
            <el-col :lg="12">
              <el-form-item label="">
                <el-input
                  :disabled="!isEdit"
                  v-model="course.short_name"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="">
            <el-input :disabled="!isEdit" v-model="course.tips"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              type="textarea"
              :disabled="!isEdit"
              v-model="course.description"
            ></el-input>
          </el-form-item>
          <el-row :gutter="20">
            <el-col :lg="12">
              <el-form-item label="">
                <el-upload
                  v-if="isEdit"
                  class="course-image-upload"
                  ref="upload"
                  action=""
                  :limit="1"
                  :file-list="fileList"
                  :before-upload="handleBeforeUpload"
                  accept="image/jpg, image/jpeg, image/png"
                >
                  <i
                    v-if="!course.image_url"
                    class="el-icon-plus el-uploader__icon"
                  ></i>
                  <img
                    v-if="course.image_url"
                    :src="course.image_url"
                    class="course-image-upload"
                  />
                </el-upload>
                <img
                  v-if="!isEdit && course.image_url"
                  :src="course.image_url"
                  class="course-image-upload"
                />
                <span v-if="!isEdit && !course.image_url"></span>
              </el-form-item>
            </el-col>
            <el-col :lg="12">
              <el-form-item label="">
                <el-switch
                  :disabled="!isEdit"
                  :active-value="1"
                  :inactive-value="0"
                  v-model="course.status"
                ></el-switch>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-col>
      <el-col :lg="12">
        <div class="sub-header">
          <div class="sub-header-title"></div>
        </div>
        <div class="chapter-container">
          <draggable
            v-model="course.chapters"
            group="chapter"
            handle=".chapter-item-draggable"
            @change="handleChapterSort"
          >
            <div
              class="chapter-item"
              v-for="(chapter, chapterIndex) in course.chapters"
              :key="chapter.id"
            >
              <div class="chapter-item-title">
                <i class="el-icon-rank chapter-item-draggable"></i>
                <span>{{ chapter.name }}</span>
                <div class="chapter-item-ctrl">
                  <el-dropdown trigger="hover" @command="handleChapter">
                    <span class="icon-ctrl">
                      <i class="el-icon-more"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item :command="'1-' + chapterIndex"
                        ></el-dropdown-item
                      >
                      <el-dropdown-item :command="'0-' + chapterIndex"
                        ></el-dropdown-item
                      >
                    </el-dropdown-menu>
                  </el-dropdown>
                </div>
              </div>
              <div class="chapter-item-section">
                <draggable
                  v-model="chapter.sections"
                  handle=".section-item-draggable"
                  group="section"
                  @change="e => handleSectionSort(e, chapterIndex)"
                >
                  <div
                    class="section-item"
                    v-for="(section, sectionIndex) in chapter.sections"
                    :key="section.id"
                  >
                    <div class="section-item-text">
                      <i class="el-icon-rank section-item-draggable"></i>
                      <span>{{ section.name }}</span>
                      <div class="section-item-ctrl">
                        <el-dropdown trigger="hover" @command="handleSection">
                          <span class="icon-ctrl">
                            <i class="el-icon-more"></i>
                          </span>
                          <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item
                              :command="
                                '2-' + chapterIndex + '-' + sectionIndex
                              "
                              ></el-dropdown-item
                            >
                            <el-dropdown-item
                              :command="
                                '1-' + chapterIndex + '-' + sectionIndex
                              "
                              ></el-dropdown-item
                            >
                            <el-dropdown-item
                              :command="
                                '0-' + chapterIndex + '-' + sectionIndex
                              "
                              ></el-dropdown-item
                            >
                          </el-dropdown-menu>
                        </el-dropdown>
                      </div>
                    </div>
                  </div>
                </draggable>
                <div
                  class="section-item"
                  @click="handleAddSection(chapterIndex)"
                >
                  <div class="section-item-create">
                    
                    <i class="el-icon-circle-plus"></i>
                  </div>
                </div>
              </div>
            </div>
          </draggable>
          <div class="chapter-item-create" @click="handleAddChapter">
            
            <i class="el-icon-circle-plus"></i>
          </div>
        </div>
      </el-col>
    </el-row>

    <el-drawer
      :visible.sync="drawerVisible"
      direction="rtl"
      size="50%"
      :before-close="handleCloseDrawer"
    >
      <div class="section-content">
        <el-form label-position="top" size="mini" :model="course">
          <el-form-item label="">
            <div class="section-title">{{ sectionInfo.name }}</div>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="sectionInfo.video_url"></el-input>
          </el-form-item>
          <el-form-item label="">
            <mavon-editor
              ref="md"
              class="md"
              :toolbars="markdownOption"
              v-model="sectionInfo.content"
              :subfield="false"
              @imgAdd="handleImgAdd"
            ></mavon-editor>
          </el-form-item>
          <el-form-item>
            <el-button size="medium" type="primary" @click="handleSaveSection"
              ></el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
import draggable from "vuedraggable";
import qiniuService from "@/globals/service/qiniu.js";
import serviceCourse from "@/globals/service/course.js";
import serviceChapter from "@/globals/service/chapter.js";
import serviceSection from "@/globals/service/section.js";

const markdownOption = {
  bold: true, // 
  mark: true, // 
  quote: true, // 
  ol: true, // 
  ul: true, // 
  link: true, // 
  imagelink: true, // 
  code: true, // code
  table: true // 
};

export default {
  data() {
    return {
      loading: true,
      markdownOption,
      isEdit: false,
      drawerVisible: false,
      fileList: [],
      course: {
        id: null,
        name: "",
        short_name: "",
        tips: "",
        description: "",
        status: 0,
        image_url: "",
        chapters: []
      },
      sectionInfo: {
        name: "",
        video_url: "",
        content: "",
        id: null
      }
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      const id = this.$route.params.id;
      serviceCourse
        .show(id)
        .then(res => {
          this.course = res.course;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    handleEdit() {
      const id = this.$route.params.id;
      this.isEdit = !this.isEdit;
      if (!this.isEdit) {
        this.loading = true;
        serviceCourse
          .edit(id, {
            ...this.course
          })
          .then(() => {
            this.$message.success("");
          })
          .finally(() => {
            this.loading = false;
          });
      }
    },
    handleBeforeUpload(file) {
      qiniuService.start(file).then(res => {
        this.course.image_url = res.imageUrl;
      });
      return Promise.reject({});
    },
    handleAddChapter() {
      const course_id = Number(this.$route.params.id);
      const sort = this.course.chapters.length + 1;
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /\S/,
        inputErrorMessage: ""
      }).then(({ value }) => {
        this.loading = true;
        serviceChapter
          .create({
            sort,
            course_id,
            name: value
          })
          .then(res => {
            this.$message.success("");
            this.course.chapters.push({
              id: res.id,
              name: value,
              sections: [],
              sort
            });
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    handleAddSection(index) {
      const chapter = this.course.chapters[index];
      const sort = chapter.sections.length + 1;
      this.$prompt("", chapter.name, {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /\S/,
        inputErrorMessage: ""
      }).then(({ value }) => {
        this.loading = true;
        serviceSection
          .create({
            sort,
            chapter_id: chapter.id,
            name: value
          })
          .then(res => {
            this.$message.success("");
            this.course.chapters[index].sections.push({
              id: res.id,
              name: value,
              sort
            });
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    handleChapter(command) {
      const commandArr = command.split("-");
      const type = commandArr[0];
      const chapterIndex = commandArr[1];
      const chapter = this.course.chapters[chapterIndex];
      if (type === "1") {
        this.$prompt("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          inputPattern: /\S/,
          inputErrorMessage: "",
          inputValue: chapter.name
        }).then(({ value }) => {
          this.loading = true;
          serviceChapter
            .edit(chapter.id, {
              name: value
            })
            .then(() => {
              this.$message.success("");
              chapter.name = value;
            })
            .finally(() => {
              this.loading = false;
            });
        });
      } else {
        this.loading = true;
        this.$confirm("" + chapter.name + "")
          .then(() => {
            return serviceChapter.destory(chapter.id);
          })
          .then(() => {
            this.course.chapters.splice(chapterIndex, 1);
          })
          .finally(() => {
            this.loading = false;
          });
      }
    },
    handleSection(command) {
      const commandArr = command.split("-");
      const type = commandArr[0];
      const chapterIndex = commandArr[1];
      const sectionIndex = commandArr[2];
      if (type === "1") {
        this.handleEditSection(chapterIndex, sectionIndex);
      } else if (type === "2") {
        this.handleOpenDrawer(chapterIndex, sectionIndex);
      } else {
        this.handleDelSection(chapterIndex, sectionIndex);
      }
    },
    handleEditSection(chapterIndex, sectionIndex) {
      const chapter = this.course.chapters[chapterIndex];
      const section = chapter.sections[sectionIndex];
      this.$prompt("", chapter.name, {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /\S/,
        inputErrorMessage: "",
        inputValue: section.name
      }).then(({ value }) => {
        this.loading = true;
        serviceSection
          .edit(section.id, {
            name: value
          })
          .then(() => {
            this.$message.success("");
            section.name = value;
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    handleDelSection(chapterIndex, sectionIndex) {
      const chapter = this.course.chapters[chapterIndex];
      const sectionName = chapter.sections[sectionIndex].name;
      const sectionId = chapter.sections[sectionIndex].id;
      this.$confirm("" + sectionName + "")
        .then(() => {
          return serviceSection.destory(sectionId);
        })
        .then(() => {
          chapter.sections.splice(sectionIndex, 1);
        });
    },
    handleChapterSort() {
      const chapters = this.course.chapters.map(data => data.id);
      serviceChapter.sort({ chapters }).then(() => {
        this.$message.success("");
      });
    },
    handleSectionSort(e, chapterIndex) {
      if (e.added || e.moved) {
        const sections = this.course.chapters[chapterIndex].sections.map(
          data => data.id
        );
        const chapter_id = this.course.chapters[chapterIndex].id;
        serviceSection.sort({ sections, chapter_id }).then(() => {
          this.$message.success("");
        });
      }
    },
    handleOpenDrawer(chapterIndex, sectionIndex) {
      const chapter = this.course.chapters[chapterIndex];
      const sectionName = chapter.sections[sectionIndex].name;
      const sectionId = chapter.sections[sectionIndex].id;
      this.drawerVisible = true;
      this.drawerTitle = sectionName;
      this.loading = true;
      serviceSection
        .show(sectionId)
        .then(res => {
          this.sectionInfo = res.section;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    handleSaveSection() {
      const { id, name, video_url, content } = this.sectionInfo;
      this.loading = true;
      serviceSection
        .edit(id, {
          name,
          video_url,
          content
        })
        .then(() => {
          this.$message.success("");
          this.drawerVisible = false;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    handleCloseDrawer(done) {
      this.$confirm("")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    handleImgAdd(pos, file) {
      qiniuService.start(file).then(res => {
        this.$refs.md.$img2Url(pos, res.imageUrl);
      });
    }
  },
  components: {
    draggable,
    mavonEditor
  }
};
</script>

